import { useState } from 'react';
import { Collapse, IconButton, TableCell, TableRow } from '@mui/material';
import { KeyboardArrowDown, KeyboardArrowUp } from '@mui/icons-material';

export const CollapseRow = ({ children, collapsedContent, ...props }) => {
  const [open, setOpen] = useState(false);

  return (
    <>
      <TableRow {...props}>
        <TableCell sx={{ width: '50px' }}>
          {collapsedContent && (
            <IconButton onClick={() => setOpen(!open)}>{open ? <KeyboardArrowUp /> : <KeyboardArrowDown />}</IconButton>
          )}
        </TableCell>
        {children}
      </TableRow>
      <TableRow>
        <TableCell colSpan={children.length + 1} padding={'none'}>
          <Collapse in={open}>{collapsedContent}</Collapse>
        </TableCell>
      </TableRow>
    </>
  );
};
