export const groupBy = (items, key) =>
  items.reduce(
    (result, item) => ({
      ...result,
      [item[key]]: [...(result[item[key]] || []), item],
    }),
    {}
  );

export const getMonthName = (index) => {
  return [
    'January',
    'February',
    'March',
    'April',
    'May',
    'June',
    'July',
    'August',
    'September',
    'October',
    'November',
    'December',
  ][index];
};

export const formatTimeSpent = (seconds) => {
  let days = Math.floor(seconds / (8 * 3600));
  seconds -= days * 8 * 3600;

  let hours = Math.floor(seconds / 3600);
  seconds -= hours * 3600;

  let minutes = Math.floor(seconds / 60);
  seconds -= minutes * 60;

  const arr = [];
  days && arr.push(`${days}d`);
  hours && arr.push(`${hours}h`);
  minutes && arr.push(`${minutes}m`);
  seconds && arr.push(`${seconds}s`);

  return arr.join(' ');
};
