import {
  Button,
  CircularProgress,
  Container,
  Grid,
  Link,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableRow,
  TextField,
  Tooltip,
  Typography,
} from '@mui/material';
import { useEffect, useState } from 'react';
import axios from 'axios';
import { formatTimeSpent, getMonthName, groupBy } from './utils';
import { CollapseRow } from './components/CollapseRow';

const client = axios.create({
  baseURL: 'https://thingproxy.freeboard.io/fetch/https://prosight.atlassian.net/rest/api/3/',
});

function App() {
  const [email, setEmail] = useState('');
  const [token, setToken] = useState('');
  const [userId, setUserId] = useState('');

  const [isLoading, setIsLoading] = useState(false);
  const [monthData, setMonthData] = useState();
  const [issues, setIssues] = useState();

  const [percentage, setPercentage] = useState();

  useEffect(() => {
    client.defaults.headers.common['Authorization'] = 'Basic ' + btoa(email + ':' + token);
  }, [email, token]);

  const fetchUserIssues = async (userId) => {
    const issues = [];

    const firstQueryRes = await client.get(`/search?jql=assignee=${userId}`);
    issues.push(...firstQueryRes.data.issues);

    for (let i = 0; i < Math.floor(firstQueryRes.data.total / firstQueryRes.data.maxResults); i++) {
      const newIssues = await client.get(`/search?jql=assignee=${userId}&startAt=${(i + 1) * 50}`);
      issues.push(...newIssues.data.issues);
    }

    return issues;
  };

  const fetchUserWorklogs = async (userId, issues) => {
    const userWorklogs = [];
    for (let i = 0; i < issues.length; i++) {
      const issue = issues[i];
      setPercentage(Math.floor((i / issues.length) * 100));
      const worklogData = await client.get(`/issue/${issue.id}/worklog`);
      const worklogs = worklogData.data.worklogs
        .filter((w) => w.author.accountId === userId)
        .map((w) => {
          w.issue = issue;
          return w;
        });
      issue.worklogs = worklogs;
      userWorklogs.push(...worklogs);
    }
    return userWorklogs;
  };

  const processWorklogs = (worklogs) => {
    for (const worklog of worklogs) {
      const date = new Date(worklog.started);
      worklog.month = date.getMonth();
      worklog.year = date.getFullYear();
      worklog.groupField = `${getMonthName(worklog.month)} (${worklog.year})`;
    }
    return groupBy(worklogs, 'groupField');
  };

  const fetchUserId = async () => {
    setIsLoading(true);

    const userData = await client.get('/myself');

    const userIssues = await fetchUserIssues(userId || userData.data.accountId);
    const userWorklogs = await fetchUserWorklogs(userId || userData.data.accountId, userIssues);
    setIssues(userIssues);

    const data = processWorklogs(userWorklogs);

    const monthData = {};
    for (const [month, worklogs] of Object.entries(data)) {
      monthData[month] = monthData[month]
        ? {
            timeSpent: monthData[month].timeSpent + worklogs.reduce((sum, el) => sum + el.timeSpentSeconds, 0),
            worklogs,
          }
        : {
            timeSpent: worklogs.reduce((sum, el) => sum + el.timeSpentSeconds, 0),
            worklogs,
          };
    }
    setMonthData(monthData);
    setIsLoading(false);
  };

  return (
    <div className="App">
      <Container>
        <Grid container spacing={3} alignItems="center">
          <Grid item>
            <TextField label="Email" value={email} onChange={(e) => setEmail(e.target.value)} />
          </Grid>
          <Grid item>
            <TextField label="Token" value={token} onChange={(e) => setToken(e.target.value)} />
          </Grid>
          <Grid item>
            <TextField label="ID" value={userId} onChange={(e) => setUserId(e.target.value)} />
          </Grid>
          <Grid item>
            <Button variant="contained" size="large" onClick={() => fetchUserId()} disabled={!token}>
              Load
            </Button>
          </Grid>
        </Grid>

        {isLoading ? (
          <Grid container spacing={2} alignItems="center" mt={4}>
            <Grid item>
              <CircularProgress />
            </Grid>
            <Grid item>
              <Typography>{percentage}%</Typography>
            </Grid>
          </Grid>
        ) : (
          monthData && (
            <Table>
              <TableHead>
                <TableRow>
                  <TableCell></TableCell>
                  <TableCell>Month</TableCell>
                  <TableCell>Time spent</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {Object.entries(monthData).map(([month, monthData]) => {
                  return (
                    <CollapseRow
                      collapsedContent={
                        <Table sx={{ background: 'whitesmoke' }}>
                          <TableHead>
                            <TableCell></TableCell>
                            <TableCell>Type</TableCell>
                            <TableCell>Key</TableCell>
                            <TableCell>Time Spent (month)</TableCell>
                            <TableCell>Remaining</TableCell>
                            <TableCell>Estimate</TableCell>
                          </TableHead>
                          <TableBody>
                            {issues.map((issue) => {
                              const thisMonthWorklogs = issue.worklogs.filter((w) => w.groupField === month);
                              if (thisMonthWorklogs.length === 0) return <></>;
                              const issueTimeSpent = thisMonthWorklogs.reduce(
                                (sum, el) => (sum += el.timeSpentSeconds),
                                0
                              );

                              return (
                                <CollapseRow
                                  collapsedContent={
                                    issue.fields?.parent &&
                                    issue.fields?.issuetype?.name === 'Sub-task' && (
                                      <Table sx={{ background: 'gainsboro' }}>
                                        <TableBody>
                                          <TableRow>
                                            <TableCell>
                                              <Link
                                                href={`https://prosight.atlassian.net/browse/${issue.fields.parent.key}`}
                                                target="_blank"
                                              >
                                                {issue.fields.parent.key}
                                              </Link>
                                            </TableCell>
                                          </TableRow>
                                        </TableBody>
                                      </Table>
                                    )
                                  }
                                >
                                  <TableCell>
                                    <Tooltip title={issue.fields?.issuetype?.name}>
                                      <img alt="type" src={issue.fields?.issuetype?.iconUrl} width={'20px'} />
                                    </Tooltip>
                                  </TableCell>
                                  <TableCell>
                                    {issue.key}
                                    {' / '}
                                    <Link href={`https://prosight.atlassian.net/browse/${issue.key}`} target="_blank">
                                      {issue.fields.summary}
                                    </Link>
                                  </TableCell>
                                  <TableCell>{formatTimeSpent(issueTimeSpent)}</TableCell>
                                  <TableCell>{formatTimeSpent(issue.fields.timeestimate)}</TableCell>
                                  <TableCell>{formatTimeSpent(issue.fields.aggregatetimeoriginalestimate)}</TableCell>
                                </CollapseRow>
                              );
                            })}
                          </TableBody>
                        </Table>
                      }
                    >
                      <TableCell>{month}</TableCell>
                      <TableCell>
                        {formatTimeSpent(monthData.timeSpent)} ({(monthData.timeSpent / 3600).toFixed(2)} hod.)
                      </TableCell>
                    </CollapseRow>
                  );
                })}
              </TableBody>
            </Table>
          )
        )}
      </Container>
    </div>
  );
}

export default App;
